import { useContext, useState } from "react";
import Modal from "../../components/Modal/Modal";
import AddCourse from "../../components/Modal/Expansion/AddCourse/AddCourse"
import "./Course.css";
import { CourseContext } from "../../Context/CourseContext";

export default function Course() {

    const [courses, actions] = useContext(CourseContext)
    const [visibleModal, setVisibleModal] = useState(false);
    const handlerModal = () => {
        setVisibleModal(!visibleModal);
    }

    return (

        <section className="courses-page">
            <div className="courses__header">
                <h2 className="page__title">Курсы</h2>
                <button onClick={handlerModal} className="btn-addStudent btn btn-custom">Добавить</button>
            </div>
            <Modal title={"Добавить курсы"} visibleModal={visibleModal} handlerModal={handlerModal}>
                <AddCourse handlerModal={handlerModal} handlerAdd={actions.addCourse} />
            </Modal>
            {
                courses.length > 0 ?
                    <div className="cards">
                        {
                            courses.map(course => (
                                <div key={course.id} className="card">
                                    <div className="card__header">
                                        <div className="card__img">
                                            <h5 className="card__title">{course.name}</h5>
                                        </div>
                                    </div>
                                    <div className="card__body">
                                        <h5 className="card__name">{course.name}</h5>
                                        <p className="card__price">{course.price}</p>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                    :
                    <div className="EmptyCourse">
                        <p className="mb-0"> Список курсов пусть. Добавьте новый</p>
                    </div>
            }

        </section>
    )
}