import { useRef, useState } from "react";
import modalCss from "../../Modal.module.css"


export default function AddCourse(props) {

    const formRef = useRef();
    const [price, setPrice] = useState("");
    const [name, setName] = useState("");
    
    const HadlerAddStudent = (e) => {
        e.preventDefault();
        props.handlerAdd({ price, name})
        formRef.current.reset();
        props.handlerModal();
    }

    return (
        <form ref={formRef} onSubmit={HadlerAddStudent} className={modalCss.modalBody}>

            {/* Full Name */}
            <div className="mb-3">
                <label className="mb-2" htmlFor="name-modal">Название</label>
                <input onChange={(e) => setName(e.target.value)} className={`${modalCss.customInput} form-control`} id="name-modal" type="text" required />
            </div>
            {/* phoneNumber */}
            <div className="mb-3">
                <label className="mb-2" htmlFor="price-modal">Цена</label>
                <input onChange={(e) => setPrice(e.target.value)} className={`${modalCss.customInput} form-control`} id="price-modal" type="number"  required />
            </div>

            {/* sumbit */}
            <button className={`${modalCss.btnCustom} btn`} type="submit">Добавить</button>
        </form>
    )
}