import { useContext, useState } from "react";
import AddStudent from "../../components/Modal/Expansion/AddStudent/AddStudent";
import Modal from "../../components/Modal/Modal";
import RowStudents from "../../components/RowStudents/RowStudents";
import { StudentsContext } from "../../Context/StudentsContext";
import "./Students.css";


export default function Students() {

    const [visibleFilter, setVisibleFilter] = useState(false);
    const [students, actions] = useContext(StudentsContext);
    const [valueName, setValueName] = useState("");
    const [valuePhone, setValuePhone] = useState("");
    const [FilterStudent, setFilterStudent] = useState([]);

    const handlerInputName = (e) => {
        setValueName(e.target.value);
    }

    const handlerInputPhonee = (e) => {
        setValuePhone(e.target.value);
    }

    const handlerVisbleFilter = () => {
        setVisibleFilter(!visibleFilter)
    }

    const handlerfilterStudent = (e) => {
        e.preventDefault();
        if (valuePhone.length === 0 && valueName.length === 0) {
            return
        }
        const tempStudent = students.filter(student =>
            (student.fullName.toLowerCase().includes(valueName.toLowerCase()) && valueName.length > 0)
            ||
            (student.phoneNumber.includes(valuePhone) && valuePhone.length > 0));

        setFilterStudent(tempStudent.length > 0 ? tempStudent : null);
    }

    const filterReset = () => setFilterStudent([]);

    const [visibleModal, setVisibleModal] = useState(false);
    const handlerModal = () => {
        setVisibleModal(!visibleModal);
    }

    return (
        <section className="page-students">
            <div className="students__header">
                <h2 className="page__title">Студенты</h2>
                <button onClick={handlerModal} className="btn-addStudent btn btn-custom">Добавить</button>
            </div>

            <Modal title={"Добавить студента"} visibleModal={visibleModal} handlerModal={handlerModal}>
                <AddStudent handlerModal={handlerModal} handlerAdd={actions.addStudent}/>
            </Modal>

            <div className="students__filter">
                <span className="filterHadler" onClick={handlerVisbleFilter}><small className={`${visibleFilter ? "icon-arrow-up" : "icon-arrow-down"}`}></small> Фильтр</span>
                <form onSubmit={handlerfilterStudent} onReset={filterReset} className={`filter__form  ${visibleFilter ? "showFilter__form" : ""}`} >

                    <div className="filter-BlockInput">
                        <label htmlFor="name">Имя</label>
                        <input onChange={handlerInputName} id="name" className="form-control custom-input" type="text" placeholder="Имя" />
                    </div>

                    <div className="filter-BlockInput">
                        <label htmlFor="phone">Телефон</label>
                        <input onChange={handlerInputPhonee} id="phone" className="form-control custom-input" type="tel" placeholder="Телефон" />
                    </div>

                    <div>
                        <button className="btn-sumbit btn btn-custom" type="submit">Отправить</button>
                        <button className="btn-reset btn btn-outline-danger" type="reset">Сбросить фильтр</button>
                    </div>
                </form>
                <hr className="line"></hr>
            </div>
            {
                //if условный конструктор
                Array.isArray(FilterStudent) && students.length > 0 ?
                    <table className="table-divided table table-borderless">
                        <thead>
                            <tr>
                                <th>Имя</th>
                                <th>Телефон</th>
                                <th>Группы</th>
                                <th>Действия</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                FilterStudent.length > 0 ?
                                    <RowStudents items={FilterStudent} deleteStudent={actions.deleteStudent} />
                                    :
                                    <RowStudents items={students} deleteStudent={actions.deleteStudent} />
                            }
                        </tbody>
                    </table>
                    : //else 
                    <div className="notFoundOrEmpty">
                        <p className="mb-0">{students.length > 0 ? "Cтудент не найден" : "Список студентов пуст. Добавьте нового"} </p>
                    </div>
            }

        </section >
    )
}