import { createContext, useState } from "react";


export const CourseContext = createContext();


export function CourseContextProvider(props) {

    const [courses, setCourse] = useState([

        //HACK временно для тест
        {
            id: 1,
            name: "Математика",
            price: "150000",
            description: "Математика",
        },
    ])

    const actions = {

        addCourse: (course) => {
            const newCourse = {
                id: Math.random(),
                name: course.name,
                price: course.price,
                description: course.description,
            }
            setCourse([...courses, newCourse]);
        },

        deleteStudent: (id) => {
            const tempCourses = courses.filter(student => student.id !== id);
            setCourse([...tempCourses]);
        }

    }


    return (
        <CourseContext.Provider value={[courses, actions]} >
            {props.children}
        </CourseContext.Provider>
    )
}