import BackDrop from "../BackDrop/BackDrop.jsx";
import modalCss from "./Modal.module.css";


export default function Modal(props) {
 
    return (
        <>
            {props.visibleModal ? <BackDrop handlerModal={props.handlerModal} /> : null}
            <div className={`${modalCss.modal} ${props.visibleModal ? modalCss.modalShow : ""}`}>
                <div className={modalCss.modalHeader}>
                    <h5>{props.title}</h5>
                    <i onClick={props.handlerModal} className="fas fa-times"></i>
                </div>
                {props.children}
            </div>
        </>
    )
}