import { useContext,useState } from "react";
import { TeachersContext } from "../../Context/TeachersContext";
import Modal from "../../components/Modal/Modal";
import AddTeacher from "../../components/Modal/Expansion/AddTeacher/AddTeacher"
import "./Teachers.css";

export default function Teachers() {

    const [teachers, actions] = useContext(TeachersContext);
    const [visibleModal, setVisibleModal] = useState(false);

    const handlerModal = () => {
        setVisibleModal(!visibleModal);
    }

    return (
        <section className="teacher-page">
            <div className="teachers__header mb-3">
                <h2 className="page__title">Учителя</h2>
                <button onClick={handlerModal} className="btn-addTeacher btn btn-custom">Добавить</button>
            </div>
            
            <Modal title={"Добавить нового учителя"} visibleModal={visibleModal} handlerModal={handlerModal}>
                <AddTeacher handlerModal={handlerModal} handlerAdd={actions.addTeacher}/>
            </Modal>
            {
                teachers.length > 0 ?
                    <table className="table-teacher table table-borderless">
                        <thead>
                            <tr>
                                <th>id</th>
                                <th>Имя</th>
                                <th>Телефон</th>
                                <th>Действия</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                teachers.map(teacher => (
                                    <tr className="RowTeacher" key={teacher.id}>
                                        <td className="first-child">{Math.round(teacher.id * 100000)}</td>
                                        <td >{teacher.fullName}</td>
                                        <td>{teacher.phoneNumber}</td>
                                        <td className="last-child">
                                            <button onClick={() => actions.deleteTeacher(teacher.id)} data-title="Удалить" type="button" className="btn btn-outline-danger btn-not-bg btn-not-br btn-icon ">
                                                <i className="fas fa-trash"></i>
                                            </button>
                                        </td>
                                    </tr>
                                ))
                            }
                        </tbody>
                    </table>

                    :

                    <div className="EmptyTeachers">
                        <p className="mb-0"> Список учителей пусть. Добавьте новый</p>
                    </div>
            }

        </section>
    )
}