import { useRef, useState } from "react";
import modalCss from "../../Modal.module.css"


export default function AddTeacher(props) {

    const formRef = useRef();
    const [phoneNumber, setphoneNumber] = useState("");
    const [fullName, setFullName] = useState("");
    const [birthday, setbirthday] = useState("");
    const [sex, setSex] = useState("");
    
    const HadlerAddStudent = (e) => {
        e.preventDefault();
        props.handlerAdd({ phoneNumber, fullName, birthday, sex })
        formRef.current.reset();
        props.handlerModal();
    }

    return (
        <form ref={formRef} onSubmit={HadlerAddStudent} className={modalCss.modalBody}>
            {/* phoneNumber */}
            <div className="mb-3">
                <label className="mb-2" htmlFor="phone-modal">Телефон</label>
                <input onChange={(e) => setphoneNumber(e.target.value)} className={`${modalCss.customInput} form-control`} id="phone-modal" type="tel" pattern="\+[0-9]{12}" required />
            </div>

            {/* Full Name */}
            <div className="mb-3">
                <label className="mb-2" htmlFor="name-modal">Имя</label>
                <input onChange={(e) => setFullName(e.target.value)} className={`${modalCss.customInput} form-control`} id="name-modal" type="text" required />
            </div>

            {/* date birth */}
            <div className="mb-3">
                <label className="mb-2" htmlFor="dateBirth-modal">Дата рождения</label>
                <input onChange={(e) => setbirthday(e.target.value)} className={`${modalCss.customInput} form-control`} id="dateBirth-modal" type="date" required />
            </div>

            {/* sex */}
            <div className="mb-3">
                <p className="mb-1">Пол</p>
                <div>
                    <input onClick={() => setSex("Мужчина")} value="man" name="sex" className={`${modalCss.customRadioButton} form-check-input`} id="man" type="radio" required />
                    <label style={{ marginRight: "20px" }} className="form-check-label" htmlFor="man">Мужчина</label>
                    <input onClick={() => setSex("Женщина")} value="woman" name="sex" className={`${modalCss.customRadioButton} form-check-input`} id="woman" type="radio" required />
                    <label className="form-check-label" htmlFor="woman">Женщина</label>
                </div>
            </div>

            {/* sumbit */}
            <button className={`${modalCss.btnCustom} btn`} type="submit">Добавить</button>
        </form>
    )
}