import "./SideBar.css";
import { NavLink } from "react-router-dom";
import Scrollbars from "react-custom-scrollbars";



export default function SideNav(props) {


    return (

        <Scrollbars autoHide >
            <nav className={`sideNav ${props.visbleSideBar ? "showSideNav" : ""}`}>
                <ul className="list-sideNav">
                    <li onClick={props.SideBarClose} className="item-list">
                        <NavLink className="SideBar__link" to="/students" activeClassName={"selected"} >
                            <i className="fas fa-user-graduate"></i>
                            <span> Студенты</span>
                        </NavLink>
                    </li>
                    <li onClick={props.SideBarClose} className="item-list">
                        <NavLink className="SideBar__link" to="/teachers" activeClassName={"selected"}>
                            <i className="fas fa-chalkboard-teacher"></i>
                            <span>Учителя</span>
                        </NavLink>
                    </li>
                    <li onClick={props.SideBarClose} className="item-list">
                        <NavLink className="SideBar__link" to="/courses" activeClassName={"selected"}>
                            <i className="fas fa-book"></i>
                            <span>Курсы</span>
                        </NavLink>
                    </li>
                </ul>
            </nav>
        </Scrollbars>

    )
}