import { useState } from "react";
import Fullscreen from 'react-fullscreen-crossbrowser';
import SideBar from "../components/SideBar/SideBar";
import TopBar from "../components/TopBar/TopBar.jsx";
import { Link } from "react-router-dom"
import Logo from "../images/Logo.png"
import "./Loyout.css"
import BackDrop from "../components/BackDrop/BackDrop";
export default function Layout(props) {

    const [isFullScreen, setFullScreen] = useState(false);
    const [visbleSideBar, setVisbleSideBar] = useState(false);

    const handlerVisbleSideBar = () => {
        setVisbleSideBar(!visbleSideBar);
    }

    const SideBarClose = () => {
        setVisbleSideBar(false);
    }

    return (
        <Fullscreen enabled={isFullScreen} onChange={x => setFullScreen(x)}>
            <div className="gridConteiner">
                <header className="header">
                    <TopBar handlerVisbleSideBar={handlerVisbleSideBar} isFullScreen={isFullScreen} setFullScreen={setFullScreen} />
                </header>
                <aside className="aside">
                    {visbleSideBar ? <BackDrop style={{ backgroundColor: "transparent" }} handlerModal={SideBarClose} /> : null}
                    <SideBar SideBarClose={SideBarClose} visbleSideBar={visbleSideBar} />
                </aside>
                <main className="main">
                    {props.children}
                </main>
                <footer className="footer">
                    <Link to="/"><i className="fas fa-headset"></i> Техническая поддержка </Link>
                    <Link to="/"><i className="far fa-question-circle"></i> Документация</Link>
                    <Link style={{ display: "block", marginLeft: "auto" }} to="/"><img style={{ height: "80px" }} src={Logo} alt="logo" /></Link>
                </footer>
            </div>
        </Fullscreen>
    )
}