import { createContext, useState } from "react";

export const TeachersContext = createContext();

export const TeacherContextProvider = (props) => {

    const [teachers, setTeachers] = useState([{
        id: Math.random(),
        phoneNumber: "+998902972077",
        fullName: "Aziz Namozov",
        birthday: Date.UTC(1997,12,6),
        sex: "xz",
    }]);

    const actions = {


        addTeacher: (teacher) => {
            const newTeacher = {
                id: Math.random(),
                phoneNumber: teacher.phoneNumber,
                fullName: teacher.fullName,
                birthday: teacher.birthday,
                sex: teacher.sex,
            }
            setTeachers([...teachers, newTeacher]);
        },

        deleteTeacher: (id) => {
            const tempTeacher = teachers.filter(teacher => teacher.id !== id);
            setTeachers([...tempTeacher]);
        }
    }

    return (
        <TeachersContext.Provider value={[teachers, actions]}>
            {
                props.children
            }
        </TeachersContext.Provider>
    )
}