import { createContext, useState } from "react";


export const StudentsContext = createContext();


export function StudentsContextProvider(props) {

    const [students, setStudents] = useState([

        //HACK временно для теста
        {
            id: 1,
            phoneNumber: "+998979175571",
            fullName: "Isomadinov Akmal",
            birthday: Date.UTC(1997, 7, 9),
            sex: "man",
            group: "Matematika Aziz",
        },
        {
            id: 2,
            phoneNumber: "+55745689456",
            fullName: "Акуцщшкцшугкцук Нушгцк",
            birthday: Date.UTC(2004, 5, 1),
            sex: "man",
            group: "Matematika Aziz",
        },
    ])

    const actions = {

        addStudent: (student) => {
            const newStudent = {
                id: Math.random(),
                phoneNumber: student.phoneNumber,
                fullName: student.fullName,
                birthday: student.birthday,
                sex: student.sex,
                group: student.group,
            }
            setStudents([...students, newStudent]);
        },

        deleteStudent: (id) => {
            const tempStudents = students.filter(student=>student.id !== id);
            setStudents([...tempStudents]);
        }

    }


    return (
        <StudentsContext.Provider value={[students, actions]} >
            {props.children}
        </StudentsContext.Provider>
    )
}