import "./TopBar.css";
import Logo from "../../images/Logo.png";
import img from "../../images/no-img.png"
import { Link } from "react-router-dom";
import SearchInput from "../SearchInput/SearchInput";
export default function TopBar(props) {

    const handlerFullScreen = () => {
        props.setFullScreen(!props.isFullScreen)
    }
    
    return (
        <nav className="top-bar">
            <div className="nav-left">
                <button onClick={props.handlerVisbleSideBar} className="topBar__menu">
                    <i className="icon-menu"></i>
                </button>
                <SearchInput/>
            </div>
            <div className="topBar__logoWrapper">
                <Link to="./">  <img className="topBar__logo" src={Logo} alt="" /></Link>
            </div>
            <div className="nav-right">
                <button onClick={handlerFullScreen} className="btn-fullscreen">
                    <i className={`${props.isFullScreen ? "icon-size-actual" : "icon-size-fullscreen"}`}></i>
                </button>
                <img className="avatar" src={img} alt="" />
            </div>
        </nav>
    )
}