import "./RowStudents.css";

export default function RowStudents(props) {

    return (
        <>
            {
                props.items.map(item => (
                    <tr className="RowStudents" key={item.id}>
                        <td className="first-child">{item.fullName}</td>
                        <td>{item.phoneNumber}</td>
                        <td>{item.group}</td>
                        <td className="last-child">
                            <button data-title="Добавить в группу" type="button" className="btn btn-outline-primary btn-not-bg btn-not-br btn-icon ">
                                <i className="fas fa-inbox"></i>
                            </button>
                            <button data-title="Добавить оплату" type="button" className="btn btn-outline-success btn-not-bg btn-not-br btn-icon">
                                <i className="fas fa-money-check-alt"></i>
                            </button>
                            <button onClick={() => props.deleteStudent(item.id)} data-title="Удалить" type="button" className="btn btn-outline-danger btn-not-bg btn-not-br btn-icon ">
                                <i className="fas fa-trash"></i>
                            </button>
                        </td>
                    </tr>
                ))
            }
        </>
    )
}